/**
 * @jsx React.DOM
 */
'use strict';



var
    React = require('react'),
    config = window.config,
    node = require('../../js/node');



var LeaderboardComponent = module.exports = React.createClass({displayName: 'exports',



    getInitialState: function() {
        return {
            players: [],
            active: true
        };
    },



    componentDidMount: function() {
        
        var _this = this;
        
        this.getLeaderboard();
        
        //node.socket.on('leaderboard.show', _this.show);
        //node.socket.on('leaderboard.hide', _this.hide);

    },
    
    
    
    show: function() {
        this.getLeaderboard();
        this.setState({
            active: true
        });
    },
    
    
    
    hide: function() {
        this.setState({
            active: false
        });
    },
    
    
    
    getLeaderboard: function() {
        var _this = this;
        $.get(config.clientUrl + '/leaderboard', function(players) {
            _this.setState({
                players: players
            });
        });
    },



    render: function() {

        var
            players,
            leaderboard;

        players = this.state.players.map(function(player, i) {
            return (
                React.DOM.li({className: "leaderboard__player", key: player.id}, 
                    React.DOM.div({className: "leaderboard__player__inner"}, 
                        React.DOM.div({className: "leaderboard__player__name"}, i + 1, ". ", player.name)
                    )
                )
            );
        });
        
        if(this.state.active) {
            leaderboard = (
                React.DOM.div({className: "leaderboard", key: "leaderboard"}, 
                    React.DOM.ol(null, 
                        players
                    )
                )
            );
        }

        return leaderboard;

    }



});