/**
 * @jsx React.DOM
 */
'use strict';



var
    React = require('react'),
    ReactCSSTransitionGroup = require('react/lib/ReactCSSTransitionGroup'),
    config = window.config,
    node = require('../js/node'),
    StatusComponent = require('./StatusComponent'),
    LeaderboardComponent = require('./leaderboard/LeaderboardComponent');


    
var StatsComponent = module.exports = React.createClass({displayName: 'exports',


    getInitialState: function() {
        return {
            fullView: true,
            playersFirstGame: undefined,
            lastGame: undefined,
            winner: undefined,
            headToHead: undefined,
            biggestWinningStreak: undefined,
            mostConsecutiveLosses: undefined,
            largestWhooping: undefined,
            totalCompanyGames: undefined,
            mostFrequentPlayer: undefined
        };
    },



    componentDidMount: function() {

        var _this = this;

        node.socket.on('stats.lastGameBetweenPlayers', function(data) {
            _this.lastGameBetweenPlayers(data.lastGame);
        });
        
        node.socket.on('stats.headToHead', function(data) {
            _this.headToHead(data.headToHead);
        });
        
        node.socket.on('stats.biggestWinningStreak', function(streak) {
            _this.setState({ biggestWinningStreak: streak });
        });
        
        node.socket.on('stats.mostConsecutiveLosses', function(streak) {
            _this.setState({ mostConsecutiveLosses: streak });
        });
        
        node.socket.on('stats.largestWhooping', function(whooping) {
            _this.setState({ largestWhooping: whooping });
        });
        
        node.socket.on('stats.totalCompanyGames', function(count) {
            _this.setState({ totalCompanyGames: count });
        });
        
        node.socket.on('stats.mostFrequentPlayer', function(player) {
            _this.setState({ mostFrequentPlayer: player });
        });
        
        node.socket.on('leaderboard.hide', _this.showCompactView);
        node.socket.on('game.end', _this.end);
        node.socket.on('game.reset', _this.reset);
        
    },
    
    
    
    end: function(data) {
        this.setState({ winner: data.winner });
        setTimeout(this.showFullView, config.winningViewDuration);
    },
    
    
    
    showFullView: function() {
        this.setState({ fullView: true });
    },
    
    
    
    showCompactView: function() {
        this.setState({ fullView: false });
    },
    
    
    
    lastGameBetweenPlayers: function(lastGame) {
        this.setState({
            lastGame: lastGame
        });
    },
    
    
    
    headToHead: function(players) {
        this.setState({
            headToHead: players
        });
    },
    
    
    
    reset: function() {
        this.setState({
            fullView: true,
            lastGame: undefined,
            playersFirstGame: undefined,
            headToHead: undefined,
            winner: undefined
        });
    },
    
    
    
    render: function() {

        var
            classes = 'stats_container clearfix',
            score,
            headToHead,
            headToHeadScore,
            firstMatch,
            leaderboard,
            logo,
            mostFrequentPlayer,
            biggestWinningStreak,
            mostConsecutiveLosses,
            largestWhooping,
            totalCompanyGames,
            winner;

        if(!this.state.fullView) {
            classes += ' compact-view';
        }

        if(this.state.playersFirstGame) {
            firstMatch = React.DOM.div({className: "stat_score"}, "Players First Match");
        }
        
        if(!this.state.fullView) {
            
            if(this.state.lastGame) {
                score = (
                    React.DOM.div({className: "stats__component", key: "last-game"}, 
                        React.DOM.span({className: "header stats__title"}, "Last Game"), 
                        React.DOM.div({className: "stat_score"}, 
                            this.state.lastGame[0].score, 
                            React.DOM.span({className: "stat_score_player"}, this.state.lastGame[0].player.name)
                        ), 
                        React.DOM.div({className: "stat_dash"}, "-"), 
                        React.DOM.div({className: "stat_score"}, 
                            this.state.lastGame[1].score, 
                            React.DOM.span({className: "stat_score_player"}, this.state.lastGame[1].player.name)
                        )
                    )
                );
            } else if(typeof this.props.player0.name !== 'undefined' &&  typeof this.props.player1.name !== 'undefined') {
                score = (
                    React.DOM.div({className: "stats__component", key: "last-game"}, 
                        React.DOM.span({className: "header stats__title"}, "Last Game"), 
                        React.DOM.div({className: "stat_score"}, 
                            "0", 
                            React.DOM.span({className: "stat_score_player"}, this.props.player0.name)
                        ), 
                        React.DOM.div({className: "stat_dash"}, "-"), 
                        React.DOM.div({className: "stat_score"}, 
                            "0", 
                            React.DOM.span({className: "stat_score_player"}, this.props.player1.name)
                        )
                    )
                );
            }
            
            if(typeof this.state.winner !== 'undefined') {
                score = (
                    React.DOM.div({className: "stats__component", key: "last-game"}, 
                        React.DOM.span({className: "header stats__title"}, "Final Score"), 
                        React.DOM.div({className: "stat_score"}, 
                            this.props.score[0], 
                            React.DOM.span({className: "stat_score_player"}, this.props.player0.name)
                        ), 
                        React.DOM.div({className: "stat_dash"}, "-"), 
                        React.DOM.div({className: "stat_score"}, 
                            this.props.score[1], 
                            React.DOM.span({className: "stat_score_player"}, this.props.player1.name)
                        )
                    )
                );
            }
            
        }
        
        if( (!this.state.fullView && this.state.headToHead) || (!this.state.fullView && this.state.winner) ) {
            
            if(typeof this.state.headToHead !== 'undefined') {
                headToHeadScore = this.state.headToHead.slice();
            } else {
                headToHeadScore = [0, 0];
            }
            
            if(typeof this.state.winner !== 'undefined') {
                headToHeadScore[this.state.winner] = this.state.headToHead[this.state.winner] + 1;
            }
            
            headToHead = (
                React.DOM.div({className: "stats__component", key: "head-to-head"}, 
                    React.DOM.span({className: "header stats__title"}, "Head To Head"), 
                    React.DOM.div({className: "stat_score"}, 
                        headToHeadScore[0], 
                        React.DOM.span({className: "stat_score_player"}, this.props.player0.name)
                    ), 
                    React.DOM.div({className: "stat_dash"}, "-"), 
                    React.DOM.div({className: "stat_score"}, 
                        headToHeadScore[1], 
                        React.DOM.span({className: "stat_score_player"}, this.props.player1.name)
                    )
                )
            );
            
        }

        if(this.state.fullView) {
            
            logo = React.DOM.img({className: "stats__logo", src: "img/logos/logo.svg", alt: "Ping Pong", key: "logo"});
            
            leaderboard = (
                React.DOM.div({className: "stats__component", key: "leaderboard"}, 
                    React.DOM.span({className: "header stats__title"}, "Leaderboard"), 
                    LeaderboardComponent(null)
                )
            );
            
            if(typeof this.state.mostFrequentPlayer !== 'undefined') {
                mostFrequentPlayer = (
                    React.DOM.div({className: "stats__component stats__component--pin-bottom", key: "league-form-player"}, 
                        React.DOM.span({className: "header stats__title"}, "Most Frequent Player"), 
                        React.DOM.div({className: "stat_score"}, this.state.mostFrequentPlayer)
                    )
                );
            }
            
            if(typeof this.state.biggestWinningStreak !== 'undefined') {
                biggestWinningStreak = (
                    React.DOM.div({className: "stats__component stats__component--bordered", key: "biggest-winning-streak"}, 
                        React.DOM.span({className: "header stats__title"}, "Biggest Winning Streak"), 
                        React.DOM.div({className: "stat_score"}, this.state.biggestWinningStreak.player), 
                        React.DOM.div({className: "stat_dash"}, "-"), 
                        React.DOM.div({className: "stat_score"}, this.state.biggestWinningStreak.streak)
                    )
                );
            }
            
            if(typeof this.state.mostConsecutiveLosses !== 'undefined') {
                mostConsecutiveLosses = (
                    React.DOM.div({className: "stats__component stats__component--bordered", key: "most-consecutive-losses"}, 
                        React.DOM.span({className: "header stats__title"}, "Most Consecutive Losses"), 
                        React.DOM.div({className: "stat_score"}, this.state.mostConsecutiveLosses.player), 
                        React.DOM.div({className: "stat_dash"}, "-"), 
                        React.DOM.div({className: "stat_score"}, this.state.mostConsecutiveLosses.streak)
                    )
                );
            }
            
            if(typeof this.state.largestWhooping !== 'undefined') {
                largestWhooping = (
                    React.DOM.div({className: "stats__component stats__component--bordered", key: "largest-whooping"}, 
                        React.DOM.span({className: "header stats__title"}, "Largest Whooping"), 
                        React.DOM.div({className: "stat_score"}, 
                            this.state.largestWhooping.scores[0], 
                            React.DOM.span({className: "stat_score_player"}, this.state.largestWhooping.players[0])
                        ), 
                        React.DOM.div({className: "stat_dash"}, "-"), 
                        React.DOM.div({className: "stat_score"}, 
                            this.state.largestWhooping.scores[1], 
                            React.DOM.span({className: "stat_score_player"}, this.state.largestWhooping.players[1])
                        )
                    )
                );
            }
            
            if(typeof this.state.totalCompanyGames !== 'undefined') {
                totalCompanyGames = (
                    React.DOM.div({className: "stats__component stats__component--pin-bottom", key: "total-company-games"}, 
                        React.DOM.span({className: "header stats__title"}, "Total Company Games"), 
                        React.DOM.div({className: "stat_score"}, this.state.totalCompanyGames)
                    )
                );
            }
            
        }

        return (
            React.DOM.div({className: classes}, 
                StatusComponent({mini: "true"}), 
                ReactCSSTransitionGroup({transitionName: "stats__logo"}, 
                    logo
                ), 
                React.DOM.div({className: "stats__inner"}, 
                    React.DOM.span({className: "title"}, "Stats"), 
                    React.DOM.div({className: "stats_left stats"}, 
                        React.DOM.div({className: "stats__group"}, 
                            ReactCSSTransitionGroup({transitionName: "stats__components"}, 
                                firstMatch, 
                                score
                            )
                        ), 
                        React.DOM.div({className: "stats__group"}, 
                            ReactCSSTransitionGroup({transitionName: "stats__components"}, 
                                leaderboard
                            )
                        )
                    ), 
                    React.DOM.div({className: "stats_right stats"}, 
                        React.DOM.div({className: "stats__group"}, 
                            ReactCSSTransitionGroup({transitionName: "stats__components"}, 
                                firstMatch, 
                                headToHead
                            )
                        ), 
                        React.DOM.div({className: "stats__group"}, 
                            ReactCSSTransitionGroup({transitionName: "stats__components"}, 
                                biggestWinningStreak, 
                                mostConsecutiveLosses, 
                                largestWhooping, 
                                mostFrequentPlayer
                            )
                        )
                    )
                )
            )
        );
    }
    

    
});